@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;1,300&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{color:darkslateblue;padding:0 10px}main{margin:50px auto;padding:0 50px;display:flex;border-radius:10px;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;font-family:"Lato", sans-serif;min-height:500px}.general-info{padding:10px;flex:1 1;position:relative;background-color:white;border-radius:10px 0 0 10px;border-right:1px solid rgba(71,61,139,0.089)}.general-info .text-view,.general-info .form-view{display:flex;flex-direction:column}.general-info p{font-weight:300;padding:5px;margin:10px 0}.general-info .form-view{margin-top:20px}.general-info .form-view label{margin:10px 0}.general-info .form-view input[type="text"],.general-info .form-view input[type="email"]{padding:5px;border:1px solid rgba(71,61,139,0.2);border-radius:5px}.general-info .form-view input[type="submit"]{margin:40px auto;background-color:#0daf0d;color:white;border:none;padding:10px 0;width:140px;border-radius:5px;cursor:pointer}.details{flex:3 1;padding:10px;border-radius:0 10px 10px 0}.details .job-fields,.details .education-fields{position:relative}.details .form-view{display:flex;flex-direction:column;max-width:80%;min-width:400px;margin:10px auto}.details .form-view label{margin:10px 0}.details .form-view textarea,.details .form-view input[type="text"]{padding:5px;border:1px solid rgba(71,61,139,0.2);border-radius:5px}.details .form-view input[type="submit"]{margin:30px auto;background-color:#0daf0d;color:white;border:none;padding:10px 0;width:140px;border-radius:5px;cursor:pointer}.details .cancel{width:80px;margin:0 auto;background:none;border:none;display:flex;align-items:center;justify-content:space-around}.details .education-experience{padding:20px;position:relative}.details .education-experience h2{border-bottom:1px solid rgba(71,61,139,0.089);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:18px}.details .education-experience .edit-experience{position:absolute;right:10px;top:25%}.details .education-experience .delete-experience{position:absolute;top:25%;right:40px}.details .description{color:rgba(71,61,139,0.836);font-weight:300;width:90%;padding:10px 0}.edit-button{position:absolute;right:10px;top:10px;cursor:pointer}.section-title{padding:10px;border-bottom:1px solid rgba(71,61,139,0.089)}.add-button{position:absolute;right:20px;top:20px;cursor:pointer}

